<template>
    <div class="brandLogosContainer">
        <h1>What tools do we use</h1>
        <div class="logos">
            <img v-for="(logo, index) in logos" :key="index" :src="logo">
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            logos: [require('@/assets/img/react.png'), require('@/assets/img/vue.png'), require('@/assets/img/node.png'), require('@/assets/img/mongo.png'), require('@/assets/img/electron.png'), require('@/assets/img/ionic.png'), require('@/assets/img/ps.png'), require('@/assets/img/ai.png'),]
        }
    },

    methods: {
        scrollReveal() {
            if(window.pageYOffset > document.querySelector('.brandLogosContainer').offsetTop - (window.innerHeight/3)) {
                document.querySelector('.brandLogosContainer').classList.add('show-brandLogosContainer')
            }
        },
    },

    mounted() {
        window.addEventListener('scroll', this.scrollReveal)
    },

    beforeDestroy() {
        window.removeEventListener('scroll', this.scrollReveal)
    },
}
</script>

<style lang="sass">
@import '@/assets/style/variables.sass'

.brandLogosContainer
    background: $backgroundColor
    width: 100vw
    height: 100vh
    display: flex
    justify-content: center
    align-items: center
    flex-direction: column
    h1, .logos
        transition: all 1s ease
        opacity: 0
        transform: translateY(30%)
    .logos
        transition-delay: .5s
    h1
        text-align: center
        font-size: 27px
        @media (max-width: $tabletWidth)
            font-size: 22px
            width: 80%
        @media (max-width: $smartphoneWidth)
            font-size: 18px
    .logos
        margin-top: 40px
        width: 36vw
        display: flex
        flex-wrap: wrap
        justify-content: center
        @media (max-width: $tabletWidth)
            width: 45vw
        @media (max-width: $smartphoneWidth)
            width: 66vw
        img
            width: 90px
            height: 90px
            padding: 10px
            margin: 10px
            box-sizing: border-box
            opacity: .8
            transition: 1s ease
            &:hover
                opacity: 1

.show-brandLogosContainer
    h1, .logos
        opacity: 1 !important
        transform: translateY(0) !important
</style>